
body {
  margin: 0px;
  margin-top: 120px;
  padding: 0px;
}
#app {
  font-family: "Verdana";
  width: 600px;
  margin: 0 auto;
}
svg {
  position: absolute;
  top: 0px;
  left: 0;
}
input[type=button] {
  margin-right: 5px;
  padding: 5px 10px;
}
.controls {
  display: grid;
  width: 300px;
  grid-gap: 10px;
  margin: 0 auto;
}
.controls > p {
  margin: 0px;
}
.config {
  width: 600px;
  margin: 0 auto;
  padding: 10px;
}
h2 {
  margin: 10px 0px;
}
.font-config {
  margin: 5px 0px;
}
.font-config > label {
  display: grid;
  text-align: right;
  grid-template-columns: 175px -webkit-max-content;
  grid-template-columns: 175px max-content;
  grid-gap: 10px;
  width: 200px;
  margin-bottom: 10px;
}
.color-config {
  display: grid;
  grid-template-columns: 250px 250px;
  grid-gap: 10px;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.info {
  width: 600px;
  margin: 0 auto;
}
.vc-chrome {
  display:inline-block;
}
svg > text {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
